export const isSortableActive = (sortable, $ctrl) =>
  sortable && (sortable.key === $ctrl.state.standingOrders.orderBy);

const statusTranslationKeys = {
  large: {
    UNAUTHORIZED: 'unauthorized',
    PARTIALLY_AUTHORIZED: 'partially_authorized',
    AUTHORIZED: 'authorized',
    REJECTED_BY_USER: 'rejected.user',
    DELETED: 'deleted',
  },
  small: {
    UNAUTHORIZED: 'U',
    PARTIALLY_AUTHORIZED: 'PA',
    AUTHORIZED: 'A',
    REJECTED_BY_USER: 'R',
    DELETED: 'D',
  },
};

/**
 * @name getStatusLabel
 * @type {function}
 *
 * @description
 * Helper to get status label
 *
 * @param {string} status Direct debit status
 * @param {string} type of status label (small, large)
 *
 * @returns {string} Translation key for status label
 */
export const getStatusLabel = (status, type = 'large') =>
  `payment.status.${statusTranslationKeys[type][status]}`;

const frequencyTranslationKeys = {
  ONCE: 'once',
  DAILY: 'daily',
  WEEKLY: 'weekly',
  MONTHLY: 'monthly',
  YEARLY: 'yearly',
};
export const getFrequencyLabel = status => `payment.frequency.${frequencyTranslationKeys[status]}`;

/**
 * @name canAuthorize
 * @type {function}
 *
 * @description
 * Helper to check if the payment status envisages
 * authorization
 *
 * @param {object} payment
 */
export const canAuthorize = payment =>
  payment.status === 'UNAUTHORIZED' || payment.status === 'PARTIALLY_AUTHORIZED';

/**
 * @name canDelete
 * @type {function}
 *
 * @description
 * Helper to check if the payment status envisages
 * deletion
 *
 * @param {object} payment
 */
export const canDelete = payment => payment.status === 'UNAUTHORIZED';

/**
 * @name filterPayments
 * @type {function}
 *
 * @description
 * Helper to filter out authorized payments
 *
 * @param {array} payments
 */
export const filterPayments = payments => payments.filter(canAuthorize);

/**
 * @name filterPaymentsDelete
 * @type {function}
 * @inner
 *
 * @description
 * Helper to filter out payments that can be deleted
 *
 * @param {array} payments
 */
const filterPaymentsDelete = payments => payments.filter(canDelete);

/**
 * @name filterPaymentsDeleteByIds
 * @type {function}
 *
 * @description
 * Helper to filter out payments that can be deleted
 *
 * @param {array} payments
 */
export const filterPaymentsDeleteByIds = (selectedIds, payments) =>
  filterPaymentsDelete(payments)
    .map(payment => payment.id)
    .filter(id => selectedIds.indexOf(id) !== -1);

/**
 * @name isSelected
 *
 * @description
 * Helper to know if the payment is selected.
 *
 * @param {string} id
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelected = (id, selState) =>
  selState.selectedPayments.some(item => item === id);

/**
 * @name isAnySelected
 *
 * @description
 * Helper to know if any payment is selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isAnySelected = selState =>
  selState.selectedPayments.length > 0;

/**
 * @name isAnySelectedDelete
 *
 * @description
 * Helper to know if selected payments could be deleted.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isAnySelectedDelete = (selState, payments) =>
  filterPaymentsDeleteByIds(selState.selectedPayments, payments).length > 0;

/**
 * @name howManySelected
 *
 * @description
 * Helper to know how many payments are selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const howManySelected = selState =>
  selState.selectedPayments.length;

/**
 * @name howManySelectedDelete
 *
 * @description
 * Helper to know how many payments are selected
 * which could be deleted.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const howManySelectedDelete = (selState, payments) =>
  filterPaymentsDeleteByIds(selState.selectedPayments, payments).length;

/**
 * @name isSelectedAllStrict
 * @inner
 *
 * @description
 * Strictly compare selected items ids and model list
 *
 * @param {array} payments
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
const isSelectedAllStrict = (payments, selState) => {
  const selected = selState.selectedPayments;

  if (payments.length !== selected.length) {
    return false;
  }

  const matches = payments.filter(item => selected.indexOf(item.id) !== -1);
  return matches.length === payments.length;
};

/**
 * @name isSelectedAll
 *
 * @description
 * Helper to know if ALL payments are selected.
 *
 * @param {array} payments
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelectedAll = (payments, selState) =>
payments && payments.length > 0 && isSelectedAllStrict(payments, selState);

/**
 * @name isSelectedPart
 *
 * @description
 * Helper to know if at least one payment is selected.
 *
 * @param {array} payments
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelectedPart = (payments, selState) =>
  isAnySelected(selState) && !isSelectedAll(payments, selState);

/**
 * @name toggleAllPayments
 *
 * @description
 * Helper to select/deselect all payments loaded.
 *
 * @param {array} payments
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const toggleAllPayments = (payments, selState) => {
  const state = selState;
  if (isSelectedAll(payments, selState)) {
    state.selectedPayments = [];
  } else {
    state.selectedPayments = payments.map(item => item.id);
  }
};

/**
 * @name deselectPayments
 *
 * @description
 * Helper to deselect all payments selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const deselectPayments = selState => {
  const state = selState;
  state.selectedPayments = [];
};

/**
 * @name toggleSelection
 *
 * @description
 * Helper to select/deselect single payment.
 *
 * @param {string} id
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const toggleSelection = (id, selState) => {
  const state = selState;

  if (isSelected(id, state)) {
    state.selectedPayments = state.selectedPayments.filter(item => item !== id);
  } else {
    state.selectedPayments.push(id);
  }
};

/**
 * @name toggleDetails
 * @type {function}
 *
 * @description
 * Show/hide (toggle) payment details row
 *
 * @param {string|null} toggler
 * @param {string} id row identification
 * @param {object} event click object
 */
export const toggleDetails = function toggleDetails(toggler, id, event) {
  const list = ['INPUT', 'BUTTON', 'A'];

  const target = event.target.nodeName.toUpperCase();
  const parent = event.target.parentElement ?
    event.target.parentElement.nodeName.toUpperCase() : null;

  if (list.indexOf(target) > -1 || list.indexOf(parent) > -1) {
    // If any action triggered on click, do not toggle details
    return toggler;
  }

  return toggler ? null : id;
};

/**
 * @name removeNotification
 *
 * @description
 * Helper to remove notification from the list
 *
 * @param {object} payment
 * @param {array} list
 * @type {function}
 * @returns {null}
 */
export const removeNotification = (notification, notificationsList) =>
  notificationsList.filter((item) => item !== notification);

/**
 * @name disabledDelete
 *
 * @description
 * Helper to know if delete action is disabled
 *
 * @param {object} payment
 * @type {function}
 * @returns {boolean}
 */
export const disabledDelete = (payment) => payment.status !== 'UNAUTHORIZED';

